html,
body,
#app {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

svg {
  /* width: 100%;
  height: 100%; */
  border: 1px solid black;
}

.country:hover {
  opacity: 0.9;
}

#tooltip {
  position: absolute;
  padding: 5px 10px;
  border: 1px solid grey;
  border-radius: 2px;
  background: white;
  opacity: 0;
}

.top-bar {
  top: 0px;
  left: 0px;
  right: 0px;
  margin: 0 auto;
  width: 500px;
  padding: 10px 20px;
  background: white;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-bottom: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  border-right: 1px solid lightgrey;
  display: flex;
  font-family: sans-serif;
  justify-content: center;
  position: absolute;
}

.top-bar__title {
  font-weight: 100;
}

.top-bar__select {
  margin: 0 10px;
  padding: 10px 5px;
  height: 60px;
  border: none;
  font-weight: 500;
  font-size: 1.5rem;
  border-bottom: 1px dashed lightgrey;
}

.top-bar__select:focus {
  outline: 0;
}
